#root
{
    border: 1px solid black;
    border-radius: 10px;
    width: 25vw;
    height: 40vh;
    margin: 10% 35%;
    padding: 10px;
}
header
{
    text-align: center;
    font-size: 36px;
    font-weight: 900;
    font-style: Montserrat;
    padding-bottom: 10px;
}
section input
{
    margin: 6.5px;
}
input 
{
    height: 5vh;
    width: 80%;
    border: 3px solid black;
}
::placeholder
{
    font-weight: bold;
    color: #000000;
}
p
{
    display: flex;
    justify-content:space-evenly;
}
p button
{
    border: 1px solid black;
    padding: 10px 12px;
    background-color: #000000;
    color: white;
    font-weight: 900;
}
button:hover
{
    cursor: pointer;
}
#Invalid span
{
    color: red;
}
#valid
{
    font-size: 50px;
}
#ncea
{
    font-weight: bold;
    font-size: 20px;
}
#success
{
    color: blue;
    font-weight: 700;
    font-size: 20px;
}